import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import {
  Formulario, Objeto, Plan, PostRespuestasRequest,
  PostRespuestasResponse, ProximaPreguntaRequest, ProximaPreguntaResponse, Indicio
} from 'src/app/shared/models/cotizacion.model';

@Injectable()
export class CotizacionService {

  private endpoint = 'http://localhost:8080/v1';

  constructor(
    private http: HttpClient
  ) { }

  getObjetos() {
    return this.http.get<Objeto[]>(this.endpoint + `/objetos`);
  }

  getObjetoByObjeto(codObjeto: string) {
    return this.http.get<Objeto>(`${this.endpoint}/objetos/${codObjeto}`);
  }

  getIndiciosByObjeto(codObjeto: string) {
    return this.http.get<Indicio[]>(`${this.endpoint}/objetos/${codObjeto}/indicios`);
  }

  getIndicioByObjetoYCodigo(codObjeto: string, codIndicio: string) {
    return this.http.get<Indicio>(`${this.endpoint}/objetos/${codObjeto}/indicios/${codIndicio}`);
  }

  getFormulario(codObjeto: string, codIndicio: string) {
    return this.http.get<Formulario>(
      `${this.endpoint}/objetos/${codObjeto}/indicios/${codIndicio}/formulario`
    );
  }

  submitRespuestas(codObjeto: string, codIndicio: string, request: PostRespuestasRequest) {
    return this.http.post<PostRespuestasResponse>(
      `${this.endpoint}/objetos/${codObjeto}/indicios/${codIndicio}/formulario/respuestas`,
      request
    );
  }

  getProximaPregunta(codObjeto: string, codIndicio: string, request: ProximaPreguntaRequest) {
    return this.http.post<ProximaPreguntaResponse>(
      `${this.endpoint}/objetos/${codObjeto}/indicios/${codIndicio}/formulario/proxima-pregunta`,
      request
    );
  }

  getProximaPreguntaMultiple(codObjeto: string, codIndicio: string, request: ProximaPreguntaRequest) {
    return this.http.post<ProximaPreguntaResponse>(
      `${this.endpoint}/objetos/${codObjeto}/indicios/${codIndicio}/formulario/proxima-pregunta?multiple=true`,
      request
    );
  }

  getCotizacion(consultaId: number) {
    return this.http.get<Plan[]>(
      `${this.endpoint}/consultas/${consultaId}/cotizacion`
    );
  }
}
